import React from 'react';
import Img from 'gatsby-image';
import { navigate } from 'gatsby';

import { screen, fonts } from '../constants';
import Button from './Button';
import Container from './Container';

function BlockImageRight({
  pretitle,
  title,
  description,
  quote,
  author,
  image,
  image_alt,
  image_width = 413,
  sub_image,
  sub_image_alt,
  sub_image_width = 303,
  ctaText,
  isFixed = false,
  grayBackground = false,
  withButton,
  opacity = 1,
  boxShadow = '',
  borderRadius = '',
  image_align = 'right',
}) {
  const defaultPropButton = {
    text: '',
    linkto: '/',
    primary: true,
    hasArrow: false,
    large: false,
  };

  withButton = Object.assign({}, defaultPropButton, withButton);

  function handleButtonTo(linkto) {
    navigate(linkto);
  }

  return (
    <div className="block-image-right">
      <Container>
        <div className="block-image-right__wrap">
          <div className="block-image-right__text">
            <div className="app__pretitle">{pretitle}</div>
            <h2 className="app__title">{title}</h2>
            <div className="block-image-right_desc">{description}</div>
            {quote && <hr className="block-image-right__separator" />}
            {quote && (
              <p className="app__desc block-image-right_quote">{quote}</p>
            )}
            {author && <div className="block-image-right_author">{author}</div>}
            {withButton.text && (
              <Button
                onClick={() => handleButtonTo(withButton.linkto)}
                {...withButton}
              >
                {withButton.text}
              </Button>
            )}
            {/* {primaryButtonText && (
          <Button
            onClick={() => handleButtonTo(primaryButtonTo)}
            primary
            large
            hasArrow
          >
            {primaryButtonText}
          </Button>
        )}
        {secondaryButtonText && (
          <Button large onClick={() => handleButtonTo(secondaryButtonTo)}>
            {secondaryButtonText}
          </Button>
        )} */}
          </div>
          <div className="app__image-container">
            <div className="app__image-group">
              <div className="app__image block-image-right_image">
                {!isFixed && <Img fluid={image} alt={image_alt} />}
                {isFixed && <Img fixed={image} alt={image_alt} />}
              </div>
              {sub_image && (
                <div className="app__subimage block-image-right_subimage">
                  {!isFixed && <Img fluid={sub_image} alt={sub_image_alt} />}
                  {isFixed && <Img fixed={sub_image} alt={sub_image_alt} />}
                </div>
              )}
            </div>
          </div>
        </div>
      </Container>
      <style jsx>{`
        .block-image-right {
          ${grayBackground ? 'background-color: #f8f9fa;' : ''}
        }

        .block-image-right__wrap {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 64px;
        }

        .block-image-right_desc {
          font-size: 20px;
          line-height: 32px;
          letter-spacing: 0.9px;
          margin-bottom: 45px;
        }

        .block-image-right__text {
          max-width: 650px;
        }

        .block-image-right__text .app__title {
          font-size: 32px;
          font-weight: 400;
          line-height: 42px;
          font-family: ${fonts.halyardDisplay};
        }

        .block-image-right__separator {
          color: #8f99ae;
          margin-bottom: 45px;
        }

        .block-image-right_quote {
          max-width: 500px;
          font-size: 31px;
          line-height: 1.29;
          letter-spacing: 0px;
          margin-bottom: 45px;
        }

        .block-image-right_author {
          font-size: 22px;
          line-height: 36px;
          letter-spacing: 0.8px;
        }

        .block-image-right__wrap .app__image-container {
          width: 619px;
          text-align: ${image_align};
        }
        .block-image-right__wrap .app__image-group {
          display: inline-block;
        }

        .block-image-right_image {
          width: ${image_width}px;
          box-shadow: ${
            boxShadow
              ? boxShadow
              : '0px 44.159px 88.3179px -44.159px rgba(0, 0, 0, 0.3), 0px 73.5983px 147.197px -29.4393px rgba(50, 50, 93, 0.25), inset 0px 1.47197px 1.47197px rgba(143, 153, 174, 0.05);'
          }
          border-radius: ${borderRadius ? borderRadius : 29.4393}px;
          opacity: ${opacity};
          max-height: 906px;
        }
        .block-image-right_subimage {
          width: ${sub_image_width}px;
          box-shadow: 0px 42.4071px 84.8142px -42.4071px rgba(0, 0, 0, 0.3),
            0px 70.6785px 141.357px -28.2714px rgba(50, 50, 93, 0.25),
            inset 0px 1.41357px 1.41357px rgba(143, 153, 174, 0.05);
          border-radius: 28px;
          opacity: ${opacity};
        }

        @media (max-width: 1140px) and (min-width: ${screen.tabletMax + 1}px) {
          .block-image-right_image {
            width: 340px;
          }
          .block-image-right_subimage {
            width: 190px;
          }
          .block-image-right_desc {
            max-width: 450px;
          }
        }

        @media (max-width: ${screen.tabletMax}px) {
          .block-image-right__wrap {
            flex-direction: column-reverse;
            margin-top: 0px;
            padding-bottom: 0px;
          }
          .block-image-right__wrap .app__image-container {
            width: 100%;
            text-align: center;
          }
          .block-image-right_image {
            width: 400px;
          }
          .block-image-right__wrap .app__image-group {
            margin-bottom: 0px;
          }
          .block-image-right_desc {
            max-width: initial;
          }
          .block-image-right__text {
            text-align: center;
            padding-top: 50px;
            padding-bottom: 50px;
          }
        }

        @media (max-width: ${screen.mobileMax}px) {
          .block-image-right_desc.app__desc {
            font-size: 28px;
            line-height: 1.29;
            letter-spacing: 0px;
          }
          .block-image-right_image {
          }
          .block-image-right__wrap .app__image-group {
            margin-bottom: 0px;
          }
          .block-image-right_image {
            width: 250px;
          }
          .block-image-right_subimage {
            width: 150px;
          }
        }
      `}</style>
    </div>
  );
}

BlockImageRight.defaultProps = {
  withButton: {
    text: '',
    primary: true,
    hasArrow: false,
    large: false,
    linkto: '',
  },
};

export default BlockImageRight;
