import React from 'react';
import { graphql, Link } from 'gatsby';

import Layout from '../../components/Layout';
import Banner from '../../components/Banner';
import Container from '../../components/Container';
import Footer from '../../components/Footer';
import SpaceDivider from '../../components/SpaceDivider';

import BlockHeaderWithImage from '../../components/BlockHeaderWithImage';
import Block4Part from '../../components/Block4Part';
import BlockImageRight from '../../components/BlockImageRight';
import BlockImageLeft from '../../components/BlockImageLeft';
import BlockLogos from '../../components/BlockLogos';
import BlockCTA from '../../components/BlockCTA';

import TimeIcon from '../../components/icons/Time';
import MoneyIcon from '../../components/icons/Money';
import ShieldIcon from '../../components/icons/Shield';

import { edgesByName } from '../../utils/gql';

export const pageQuery = graphql`
  {
    supportGrowthImage: file(name: { eq: "support-growth" }) {
      childImageSharp {
        fluid(maxWidth: 480) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    repeatCustomersImage: file(name: { eq: "repeat-customers" }) {
      childImageSharp {
        fluid(maxWidth: 480) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    biggerBuyersImage: file(name: { eq: "bigger-buyers" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    commonLivingSmall: file(name: { eq: "common-living-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    homeFurnishingsSmall: file(name: { eq: "home-furnishings-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    simpleTireSmall: file(name: { eq: "simple-tire-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
  }
`;

export default function FeaturesPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);

  return (
    <Layout>
      <Banner />
      <Container>
        <BlockHeaderWithImage
          title="What is Resolve?"
          subtitle="Features"
          image={data.repeatCustomersImage.childImageSharp.fluid}
          desc="Resolve is an integrated platform that helps you grow B2B sales, get paid faster, and reduce risk by streamlining your net terms, accounts receivable, and payments processes."
          primaryButtonText="Get started"
          primaryButtonTo="/contact-sales/"
        />

        <SpaceDivider height={50} />

        <BlockLogos
          pretitle="Trusted by the world's most innovative companies"
          data={[
            {
              image: companies['Vice Golf'],
              name: 'Vice Golf',
              offsetTop: 0,
            },
            {
              image: companies['Article'],
              name: 'Article',
            },
            {
              image: companies['OnDemand Tires'],
              name: 'OnDemand Tires',
              offsetTop: 0,
            },
            {
              image: companies['Archipelago Lighting'],
              name: 'Archipelago Lighting',
              offsetTop: 0,
            },
            {
              image: companies['Linus Bike'],
              name: 'Linus Bike',
              offsetTop: 0,
            },
            {
              image: companies['QuietKat'],
              name: 'QuietKat',
              offsetTop: 0,
            },
          ]}
        />
        <SpaceDivider height={50} />

        <BlockImageRight
          image={data.biggerBuyersImage.childImageSharp.fluid}
          title={<span>Grow sales</span>}
          description={
            <span>
              Our credit lines increase AOV and cause your customers to keep
              coming back to order more. Merchants that have leveraged Resolve
              have seen an average increase in AOV of 20%.
            </span>
          }
          withButton={{
            text: 'Learn more',
            linkTo: '/grow-sales',
            hasArrow: true,
            large: true,
          }}
        />

        <SpaceDivider height={50} />

        <BlockImageLeft
          image={data.biggerBuyersImage.childImageSharp.fluid}
          title={<span>Get paid faster</span>}
          description={
            <span>
              Our <Link to="/advance-pay/">Advance Pay</Link> lets you offer Net
              30-60-90 to your buyers while unlocking working capital anytime by
              drawing funds from invoices submitted through the platform.
              Customers that use Resolve to offer net-terms can expect up to a
              2x increase in cashflow.
            </span>
          }
          ctaText="Learn more"
          ctaButtonTo="/advance-pay/"
        />

        <SpaceDivider height="100" />

        <Block4Part
          title="Reduce risk"
          desc={
            <span>
              You no longer have to "be the bank to your customers". Resolve
              helps you make credit decisions, unlock working capital, and get
              paid. Spend less time worrying about when you're getting paid, and
              more time on growing your business.
            </span>
          }
          items={[
            {
              title: 'Credit Assessments',
              content: (
                <div>
                  Assess the risk of your current and future customers to
                  determine their credit worthiness.
                </div>
              ),
              icon: <TimeIcon />,
            },
            {
              title: 'Invoice Chasing',
              content: (
                <div>
                  Waste less time on collections processes with automated and
                  manual follow-ups.
                </div>
              ),
              icon: <MoneyIcon />,
            },
            {
              title: 'Collections Network',
              content: (
                <div>
                  For your most troublesome A/R challenges, tap into Resolve's
                  collections network to maximize recoveries.
                </div>
              ),
              icon: <ShieldIcon width="32" height="32" />,
            },
          ]}
          ctaText="Learn more"
          ctaButtonTo="/optimize-accounts-receivable/"
        />

        <SpaceDivider height={100} />

        <BlockCTA
          pretitle="Ready to get started?"
          title="B2B companies love Resolve, but don't take our word for it"
          primaryButtonText="Get started"
          primaryButtonTo="/contact-sales/"
        />
      </Container>
      <Footer />
    </Layout>
  );
}
